import React from 'react'
import cardData from "./studioData"
import StudioCard from "./studioCard"

function StudyYears() {

    return (
        <div className="studio-1-body">
            <div className="flex-between studio-1 container">
                <span className="absImg absImg1" />
                <span className="absImg absImg2" />
                <span className="absImg absImg3" />
                <span className="absImg absImg4" />
                {cardData.map((item, index) => (<StudioCard data={item} key={index} />))}
            </div>
        </div>
    );
}

export default StudyYears;